export const validate = (values: any) => {
  const errors: any = {};
  if (!values.city) {
    errors.city = ' ';
  }
  if (!values.country) {
    errors.country = ' ';
  }
  if (!values.address) {
    errors.address = ' ';
  }
  if (!values.phone_mobile) {
    errors.phone_mobile = ' ';
  }
  return errors;
};