export const validate = (values: any) => {
  const errors: any = {};
  if (!values.name) {
    errors.name = ' ';
  }
  if (!values.email) {
    errors.email = ' ';
  }
  if (!values.phone) {
    errors.phone = ' ';
  }
  if (!values.password) {
    errors.password = ' ';
  }
  if (!values.c_password) {
    errors.c_password = ' ';
  }
  if (values.password !== values.c_password) {
    errors.c_password = '  '
  }
  if (!values.description) {
    errors.description = ' ';
  }
  return errors;
};