const humanizeStatus = (status: string) => {
    switch(status) {
        case 'pending':
            return ' '
        case 'disabled':
            return ''
        case 'paid':
            return ''
        case 'deleted':
            return ''
        case 'closed':
            return ''
        case 'active':
            return ''
        case 'declined':
            return ''
        case 'sent':
            return ''
        case 'delivered':
          return ''
        case 'error':
            return ' '
        default:
            return ''
    }
  }
  export default humanizeStatus
  